import React from "react"
import {Card, Row, Col} from "reactstrap"
import '../styles/product-variant.scss'
import imProduct from "../../../assets/images/imgs/im_product.png"
import CommonFooter from "../../../components/CommonFooter"
import ProductVariantInformation from "../components/ProductVariantInformation"

const ProductVariantCreate = () => {
    return (
        <div>
            <Row>
                <Col lg={4} sm={12}>
                    <Card className={'product-variant__product product-variant__border'}>
                        <h1 className={'title'}>Sản phẩm</h1>
                        <div className={'product-variant__product-content d-flex align-items-center'}>
                            <img src={imProduct} alt="imgProduct"/>
                            <p className={'product-variant__product-name'}>SP340 - Áo Polo Basic Premium Cotton</p>
                        </div>
                    </Card>
                    <Card className={'product-variant__border product-variant'}>
                        <h1 className={'title'}>Phiên bản</h1>
                        <div className={'product-variant__content'}>
                            <div className={'item d-flex align-items-center'}>
                                <img src={imProduct} alt="imgProductVariant"/>
                                <p className={'mb-0'}>M - L - XL - XXL  - Vàng</p>
                            </div>
                        </div>
                    </Card>
                </Col>
                <ProductVariantInformation />
            </Row>
            <CommonFooter handleBack={() => {}} handleSubmit={() => {}}/>
        </div>
    )
}
export default ProductVariantCreate