import {Button, Card, Col, FormGroup, Input, InputGroup, InputGroupAddon, Label, Row} from "reactstrap"
import React, {useState} from "react"
import Uppy from "@uppy/core"
import thumbnailGenerator from "@uppy/thumbnail-generator"
import {DragDrop} from "@uppy/react"
import icDelete from "../../../assets/images/icons/icons/ic_delete.svg"
import imgEmpty from "../../../assets/images/icons/icons/ic_upload_img.svg"
import videoEmpty from "../../../assets/images/icons/icons/ic_video_empty.svg"
import {Play} from "react-feather"

const ProductVariantInformation = props => {
    const {} = props
    const [product_variant_images, setProductVariantImages] = useState([])
    const [product_variant_video, setProductVariantVideo] = useState({})
    const [isEdit, setEdit] = useState(false)

    const imageUppy = new Uppy({
        meta: {type: 'avatar'},
        autoProceed: true,
        restrictions: {allowedFileTypes: ['image/*']}
    })

    imageUppy.use(thumbnailGenerator)

    imageUppy.on('thumbnail:generated', (file, preview) => {
        const arr = {file: file, preview: preview}
        product_variant_images.push(arr)
        setProductVariantImages([...product_variant_images])
        // product.product_images = product_variant_images
    })

    function deleteImage(event, product_image) {
        product_variant_images.splice(product_variant_images.indexOf(product_image), 1)
        setProductVariantImages([...product_variant_images])
        // product.product_images = product_images
    }

    const imageUppy2 = new Uppy({
        meta: {type: 'avatar'},
        autoProceed: true,
        restrictions: {allowedFileTypes: ['image/*']}
    })

    imageUppy2.use(thumbnailGenerator)

    imageUppy2.on('thumbnail:generated', (file, preview) => {
        const arr = {file: file, preview: preview}
        product_variant_images.push(arr)
        setProductVariantImages([...product_variant_images])
        // product.product_images = product_images
    })

    const renderPreview = () => {
        if (product_variant_images.length) {
            return (
                <div className={'product-variant-image__container-grid'}>
                    {
                        product_variant_images.map((product_image, index) => (
                            <div key={index} className={'rounded'}>
                                <div className={'image'}>
                                    <img
                                        src={product_image?.thumb_url ? product_image?.thumb_url : product_image?.preview}
                                        alt="imgProduct" className={'img'}/>
                                    {
                                        <div className={'btn-delete'}>
                                            <img src={icDelete} alt="delete" onClick={e => {
                                                deleteImage(e, product_image)
                                            }}/>
                                        </div>
                                    }
                                </div>
                            </div>
                        ))
                    }
                </div>
            )
        } else {
            return (
                <div className={'empty'}>
                    <div className={'empty-image'}>
                        <img className={'img-contain'} src={imgEmpty} alt="empty"/>
                    </div>
                    <DragDrop uppy={imageUppy2}/>
                </div>
            )
        }
    }

    const videoUppy = new Uppy({
        meta: {type: 'video'},
        autoProceed: true,
        restrictions: {allowedFileTypes: ['video/*']}
    })

    videoUppy.on('file-added', async (file) => {
        const variantVideo = {}
        variantVideo.url = URL.createObjectURL(file.data)
        variantVideo.video_file = file.data
        variantVideo.media_type = 'video'
        setProductVariantVideo(variantVideo)
        setEdit(true)
    })

    const videoUppy2 = new Uppy({
        meta: {type: 'video'},
        autoProceed: true,
        restrictions: {allowedFileTypes: ['video/*']}
    })

    videoUppy2.on('file-added', async (file) => {
        const variantVideo = {}
        variantVideo.url = URL.createObjectURL(file.data)
        variantVideo.video_file = file.data
        variantVideo.media_type = 'video'
        setProductVariantVideo(variantVideo)
        setEdit(true)
    })

    function deleteVideo(event) {
        setProductVariantVideo({})
        setEdit(true)
    }

    const renderVideoPreview = () => {
        if (product_variant_video && product_variant_video?.url) {
            return (
                <div className={'rounded'}>
                    <div className={'video-contain position-relative'}>
                        <video autoPlay playsInline loop className={'video'}>
                            <source src={product_variant_video?.url}/>
                        </video>
                        {
                            <div className={'btn-delete'}>
                                <img src={icDelete} alt="delete" className={'icon-delete'} onClick={e => {
                                    deleteVideo(e)
                                }}/>
                            </div>
                        }
                    </div>
                </div>
            )
        } else {
            return (
                <div className={'empty'}>
                    <div className={'empty-image text-center'}>
                        <img className={'img-contain'} src={videoEmpty} alt="empty"/>
                    </div>
                    <DragDrop uppy={videoUppy2}/>
                </div>
            )
        }
    }

    return (
        <Col lg={8} sm={12}>
            <Card className={'product-variant__create-new product-variant__border'}>
                <h1 className={'title'}>Thêm mới</h1>
                <div className={'product-variant__content'}>
                    <Row>
                        <Col lg={6} sm={12}>
                            <FormGroup className={'form-group'}>
                                <Label for='name'>Tên phiên bản</Label>
                                <Input type='text' id='name' placeholder='Nhập tên phiên bản' autoComplete={'off'}/>
                            </FormGroup>
                            <FormGroup className={'form-group'}>
                                <Label for='name'>Kích thước</Label>
                                <Input type='text' id='size' placeholder='Nhập kích thước' autoComplete={'off'}/>
                            </FormGroup>
                            <FormGroup className={'form-group'}>
                                <Label for='name'>Màu sắc</Label>
                                <Input type='text' id='color' placeholder='Nhập màu sắc' autoComplete={'off'}/>
                            </FormGroup>
                        </Col>
                        <Col lg={6} sm={12} className={'product-variant-image'}>
                            <div>
                                <div className='d-flex align-items-center justify-content-between label'>
                                    <p className={'mb-0'}>Hình ảnh</p>
                                    <div className={'product-variant-image__link'}>
                                        <DragDrop uppy={imageUppy}
                                                  locale={{
                                                      strings: {dropHereOr: "Thêm hình ảnh"}
                                                  }}
                                        />
                                    </div>
                                </div>
                                <div className={'product-variant-image__container'}>
                                    {renderPreview()}
                                </div>
                            </div>
                            <div className={'pt-1'}>
                                <div className='d-flex align-items-center justify-content-between mb-50'>
                                    <p className={'mb-0'}>Video</p>
                                    <div className={'product-variant-image__link'}>
                                        <DragDrop uppy={videoUppy}
                                                  locale={{
                                                      strings: {dropHereOr: "Thêm video"}
                                                  }}
                                        />
                                    </div>
                                </div>
                                <div className={'product-variant-image__container'}>
                                    {renderVideoPreview()}
                                </div>
                            </div>
                        </Col>
                    </Row>
                </div>
            </Card>

            <Card className={'product-variant__create-new product-variant__border'}>
                <Row>
                    <Col lg={6} sm={12}>
                        <FormGroup className={'form-group pt-0'}>
                            <Label for='name'>Mã SKU</Label>
                            <Input type='text' id="sku" placeholder='Nhập mã SKU' autoComplete={'off'}/>
                        </FormGroup>
                        <FormGroup className={'form-group'}>
                            <Label for='name'>Khối lượng</Label>
                            <InputGroup>
                                <Input placeholder='Nhập khối lượng' />
                                <InputGroupAddon addonType='append' className='d-flex align-items-center flex-column justify-content-center bg-grey'>
                                    <Button className='pt-0 pb-0 bg-transparent'>
                                        <Play size={10} className={'btn-icon-top bg-icon'} color={'blue'}/>
                                    </Button>
                                    <Button className={'pt-0 pb-0 bg-transparent'}>
                                        <Play size={10} className={'btn-icon-bottom bg-icon'} color={'blue'}/>
                                    </Button>
                                </InputGroupAddon>
                            </InputGroup>
                        </FormGroup>
                    </Col>
                    <Col lg={6} sm={12}>
                        <FormGroup className={'form-group pt-0'}>
                            <Label for='name'>Mã vạch</Label>
                            <Input type='text' id='code' placeholder='Nhập mã vạch' autoComplete={'off'}/>
                        </FormGroup>
                        <FormGroup className={'form-group'}>
                            <Label for='name'>Đơn vị tính</Label>
                            <Input type='text' id='unit' placeholder='Nhập đơn vị tính' autoComplete={'off'}/>
                        </FormGroup>
                    </Col>
                </Row>
            </Card>
        </Col>
    )
}
export default ProductVariantInformation